.image-view {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.image-view > img {
    width: 100%;
    height: 98%;
}

.return-btn {
    position: fixed;
    left: 48%;
    border: none;
    background-color: transparent;
    width: 4%;
}

.return-btn-img {
    width: 100%;
    min-width: 20px;
}

.home-btns {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-align: center;
        align-items: center;
}

.camera-btn {
    width: 5%;
    position: absolute;
    bottom: 0;
    padding: 1%;
}

.photo-screen {
    width: 100%;
}

.photo-header {
    position: absolute;
    top: 2%;
    width: 98%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.photo-header > button {
    border: none;
    background-color: transparent;
    width: 3%;
    font-size: 20px;
}

.arrow-btn-img {
    width: 100%;
}

.photo-footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}



